<template>
	<view class="diy_menu">
		<view class="row">
			<view v-for="(o, i) in list" :key="i" :class="'col-' + col">
				<navigator :url="o.url" class="menu" hover-class="hover">
					<image style="width: 100%; height: auto;" :src="o.img" mode="widthFix"></image>
					<text>{{ o[vm.title] }}</text>
				</navigator>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				default: function() {
					return [];
				}
			},
			vm: {
				type: Object,
				default: function() {
					return {
						img: "img",
						title: "title",
						url: "url"
					}
				}
			},
			span: {
				type: Number,
				default: 4
			}
		},
		data() {
			var col = 12 / this.span;
			return {
				col
			}
		}
	}
</script>

<style scoped>
	image {
		border-radius: 1rem;
		overflow: hidden;
	}

	.diy_menu {
		text-align: center;
	}

	.menu {
		padding: 0.75rem;
	}
	
	.menu text {
		font-size: 0.875rem;
	}
</style>
